html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-dribbble {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-fb {
  width: 0.46em;
  height: 1em;
  fill: initial;
}

.icon-g + {
  width: 1.62em;
  height: 1em;
  fill: none;
}

.icon-instagram {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-medium {
  width: 1.15em;
  height: 1em;
  fill: initial;
}

.icon-twitter {
  width: 1.25em;
  height: 1em;
  fill: initial;
}

@font-face {
  font-family: "Arial";
  src: url("../fonts/Arial.woff2") format("woff2"), url("../fonts/Arial.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Arial-Bold";
  src: url("../fonts/Arial-Bold.woff2") format("woff2"), url("../fonts/Arial-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  background: #fff;
  min-width: 320px;
  font-size: 14px;
  color: #333;
  font-family: "Arial", Helvetica, sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  cursor: pointer;
}

button {
  background: transparent;
}

img {
  max-width: 100%;
}

.text-center {
  text-align: center;
}

.color-gray {
  color: rgba(51, 51, 51, 0.4);
}

.color-white {
  color: #fff;
}

.play {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
  text-align: left;
  cursor: pointer;
  transition: opacity 0.3s;
}

.play:after {
  content: " ";
  display: table;
  clear: both;
}

.play:hover {
  opacity: 0.7;
}

.play__icon {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #6d4da0;
  margin-right: 15px;
}

.play__icon:after {
  content: '';
  margin-left: 2px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 0 4px 6px;
  border-color: transparent transparent transparent #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.play__text {
  display: inline-block;
  color: #000;
  padding-top: 10px;
  line-height: 2;
  font-family: "Arial-Bol",  Helvetica, sans-serif;
  font-weight: 700;
}

.play__desc {
  display: block;
  color: rgba(0, 0, 0, 0.4);
}

.lines {
  font-size: 0;
  margin: 0 -2px;
}

.lines span {
  display: inline-block;
  width: 2px;
  height: 38px;
  background: #000;
  margin: 0 2px;
}

.label {
  display: inline-block;
  vertical-align: middle;
  font-size: 10px;
  color: #fff;
  background: #0ca940;
  border-radius: 5px;
  line-height: 1.1;
  margin: 0 10px;
  padding: 0 5px;
}

hr {
  margin: 0;
  border-width: 1px;
  border-color: rgba(224, 224, 224, 0.1);
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

.l {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
}

.l__col {
  max-width: 25%;
  flex-basis: 25%;
}

.l__col_gap {
  margin-left: 25%;
}

.l__col_gap-md {
  margin-left: 50%;
}

.l__col_center {
  align-self: center;
}

h1,
.h1 {
  color: #333;
  font-size: 72px;
  text-transform: uppercase;
  line-height: 1.15;
  font-family: "Arial-Bol",  Helvetica, sans-serif;
  font-weight: 700;
}

h2,
.h2 {
  font-size: 36px;
  text-transform: uppercase;
  font-family: "Arial-Bol",  Helvetica, sans-serif;
  font-weight: 700;
}

h3,
.h3 {
  font-size: 26px;
  line-height: 1.2;
  text-transform: uppercase;
  font-family: "Arial-Bol",  Helvetica, sans-serif;
  font-weight: 700;
}

h4,
.h4 {
  font-size: 20px;
  line-height: 1.2;
  text-transform: uppercase;
  font-family: "Arial-Bol",  Helvetica, sans-serif;
  font-weight: 700;
}

h5,
.h5 {
  font-size: 16px;
  line-height: 1.2;
}

.header {
  position: relative;
  padding: 20px;
  background: #f8f8f8;
}

.header__logo {
  position: absolute;
  left: 20px;
  top: calc(20px + 3px);
}

.header__right {
  position: absolute;
  right: 20px;
  top: 20px;
}

.header__link {
  color: #333;
  font-size: 14px;
  transition: opacity 0.15s;
}

.header__link:not(:last-child) {
  margin-right: 55px;
}

.header__link:hover {
  opacity: 0.4;
}

.header__link_mod {
  color: rgba(51, 51, 51, 0.4);
}

.footer {
  background: #1c1c1c;
}

.footer__row {
  padding: 40px 0 60px;
}

.footer__row_mod {
  padding: 100px 0 30px;
}

.logo {
  display: inline-block;
  vertical-align: top;
  transition: opacity 0.15s;
}

.logo:hover {
  opacity: 0.8;
}

.section {
  position: relative;
  padding: 80px 0;
}

.section__inner {
  position: relative;
}

.section__title {
  margin-bottom: 60px;
}

.section__caption {
  max-width: 940px;
}

.section__width {
  max-width: 240px;
  margin: 80px auto 90px;
  line-height: 3;
}

.section__desc {
  color: #000;
  opacity: 0.8;
  max-width: 350px;
  margin: 0 auto;
}

.section__vertical {
  display: inline-block;
  width: 19px;
}

.section__vertical > a {
  display: inline-block;
  white-space: nowrap;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}

.section__left {
  position: absolute;
  left: 20px;
  top: 50%;
  margin-top: -55px;
  transform: translateY(-50%);
}

.section__play {
  position: absolute;
  top: -4px;
  right: 0;
  width: calc(100% / 4);
}

.section__text-right {
  position: absolute;
  right: 42px;
  bottom: 120px;
}

.section__meta {
  position: absolute;
}

.section__meta_top {
  top: 40px;
  left: 40px;
}

.section__meta_bottom {
  bottom: 100px;
  right: 100px;
}

.section__item {
  position: relative;
  padding: 40px;
}

.section__item.bg-light-blue .h2 {
  color: #333;
}

.section__item.bg-light-blue .section__meta a {
  color: rgba(51, 51, 51, 0.4);
}

.section__item.bg-green .h2 {
  color: #fff;
}

.section__item.bg-green .section__meta {
  color: rgba(255, 255, 255, 0.6);
}

.section_row {
  display: flex;
  flex-flow: row nowrap;
  padding: 0;
}

.section_row .section__item {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
  max-width: 50%;
  flex-basis: 50%;
  height: 450px;
}

.section_md {
  padding-bottom: 150px;
  overflow: hidden;
}

.section_sm {
  padding: 40px 0;
}

.bg-gray {
  background: #f8f8f8;
}

.bg-green {
  background: #0ca940;
}

.bg-red {
  background: #ee2c1d;
}

.bg-light-blue {
  background: #e8ebf2;
}

.teams {
  margin: 110px 0px 145px;
}

.teams__item {
  display: inline-block;
  vertical-align: middle;
  margin: 0 10px 15px;
  border-radius: 50%;
  overflow: hidden;
  width: 100px;
  height: 100px;
}

.list__title {
  font-size: 14px;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 20px;
  font-family: "Arial-Bol",  Helvetica, sans-serif;
  font-weight: 700;
}

.list__link {
  display: block;
  color: rgba(255, 255, 255, 0.2);
  font-size: 14px;
  line-height: 2.2;
  font-family: "Arial-Bol",  Helvetica, sans-serif;
  font-weight: 700;
  transition: opacity 0.15;
}

.list__link:hover {
  opacity: 0.8;
}

.list__item {
  line-height: 2.2;
  font-family: "Arial", Helvetica, sans-serif;
  font-weight: 400;
}

.list_md .list__link {
  line-height: 2.8;
}

.list_row .list__item {
  display: inline-block;
  vertical-align: middle;
}

.list_row .list__item:not(:last-child) {
  margin-right: 15px;
}

.list_title .list__title {
  margin-bottom: 20px;
  min-height: 30px;
}

.list_dark .list__title {
  color: #000;
}

.list_dark .list__item {
  color: #000;
  opacity: 0.4;
}

.list_light .list__title {
  margin-bottom: 35px;
  line-height: 1.4;
}

.list_light .list__item {
  color: rgba(255, 255, 255, 0.6);
  font-family: "Arial", Helvetica, sans-serif;
  font-weight: 400;
}

.application {
  font-size: 0;
}

.application__item {
  display: inline-block;
  overflow: hidden;
  border-radius: 5px;
  transition: opacity 0.15;
}

.application__item:hover {
  opacity: 0.8;
}

.application__item:not(:last-child) {
  margin-right: 20px;
}

.copyright {
  color: rgba(153, 153, 153, 0.4);
  line-height: 2.2;
}

.socials-wrap {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.socials-wrap__title {
  flex: 1 0 auto;
  color: rgba(153, 153, 153, 0.4);
  line-height: 2.2;
  margin-right: 20px;
}

.socials {
  flex: 1 0 auto;
  font-size: 0;
  margin: 0 -9px;
  white-space: nowrap;
}

.socials__item {
  display: inline-block;
  vertical-align: middle;
  margin: 0 9px;
}

.socials__link {
  display: block;
  height: 100%;
}

.socials__link:hover .icon {
  fill: rgba(255, 255, 255, 0.3);
}

.socials .icon {
  fill: rgba(255, 255, 255, 0.2);
  font-size: 14px;
  max-height: 13px;
  max-width: 21px;
}

.desc {
  color: #000;
  font-size: 14px;
  line-height: 2.1;
  padding-top: 50px;
}

.desc_white {
  color: #fff;
}

.desc_top {
  padding-top: 0;
}

.tabs__caption {
  margin-bottom: 75px;
}

.tabs__button {
  line-height: 1;
  border-bottom: 2px solid transparent;
  color: #fff;
  font-family: "Arial-Bol",  Helvetica, sans-serif;
  font-weight: 700;
  transition: opacity 0.3s;
}

.tabs__button:after {
  content: '';
  font-size: 26px;
  line-height: 1.2;
  text-transform: uppercase;
  font-family: "Arial-Bol",  Helvetica, sans-serif;
  font-weight: 700;
}

.tabs__button:hover {
  opacity: 0.8;
  cursor: pointer;
}

.tabs__button.is-active {
  border-color: #f7f5f5;
}

.tabs__button:first-child {
  text-align: right;
  padding: 0 20px 17px 110px;
}

.tabs__button:first-child::after {
  content: 'problem';
}

.tabs__button:last-child {
  text-align: left;
  padding: 0 110px 17px 20px;
}

.tabs__button:last-child::after {
  content: 'solution';
}

.tabs__title {
  text-align: center;
  margin-bottom: 80px;
}
